import { Button, TextInput } from "@react-native-material/core";
import { HeaderBackButton } from "@react-navigation/elements";
import TaskModel from "models/task/task-model";
import {
  TaskRepeatMediator,
  TaskRepeatRawDataMediator
} from "models/task/task-repeat-mediator";
import { TaskValidator } from "models/task/task-validator";
import * as React from "react";
import { useEffect } from "react";
import { StyleSheet, View } from "react-native";
import { Dropdown } from "react-native-element-dropdown";
import { connect, useDispatch } from "react-redux";
import Chip from "utils/chip";
import HideKeyboard from "utils/hide-keyboard";
import { log } from "utils/log";
import TaskTimeDialog from "../components/task/task-time-dialog";
import { constants, layoutConstants } from "../models/contants";
import { createTaskCategoryService } from "../services/task-category-service";
import { createTaskService } from "../services/task-service";
import { Amplify } from 'aws-amplify';


// data model: task
// view props: visible
// action: onClose, onSave, onDelete, onCancel
function TaskDetailScreen({ navigation, route, displayCategories }) {
  const { params } = route;
  // on init create form, task date, task time, task repeat are null
  // on init update form, task date, task time, task repeat are updated base on task
  // on action create/update form, task date, task time, task repeat (null/exist value) are submited
  const task = params?.task ? JSON.parse(params?.task) : null;
  const categoryId = params?.categoryId || constants.MY_ACTIONS;
  const [title, setTitle] = React.useState(task?.title || "");
  const [description, setDescription] = React.useState(task?.description || "");
  const [displayDateTime, setDisplayDateTime] = React.useState(null);
  const [categoryUpdate, setCategoryUpdate] = React.useState(null);
  const [isFocus, setIsFocus] = React.useState(false);
  // TaskTimeDialog
  const [taskTimeDialogVisible, setTaskTimeDialogVisible] =
    React.useState(false);
  // on action add time click, task date, task time, task repeat are updated base on default
  // on action add time click + chip, task date, task time, task repeat are updated base on chip data saved
  // on action update chip click, task date, task time, task repeat are updated base on chip data saved
  // on action close chip click, task date, task time, task repeat are updated to null
  const [taskTimeDialogDate, setTaskTimeDialogDate] = React.useState(
    task?.date ? new Date(task.date) : null
  );
  const [taskTimeDialogTime, setTaskTimeDialogTime] = React.useState(
    task?.time ? new Date(task.time) : null
  );
  const [taskTimeDialogRepeat, setTaskTimeDialogRepeat] = React.useState(
    task?.selectedDaysRepeat || null
  );
  const stateRef = React.useRef();
  stateRef.current = {
    title,
    description,
    categoryUpdate,
    taskTimeDialogDate,
    taskTimeDialogTime,
    taskTimeDialogRepeat,
  };
  const dispatch = useDispatch();

  const isUpdate = !!task;
  const taskService = createTaskService();
  const taskCategoryService = createTaskCategoryService();

  useEffect(() => {
    navigation.setOptions({
      headerLeft: (props) => (
        <HeaderBackButton
          {...props}
          onPress={saveAndBack}
          label={"Actions"}
          labelVisible={true}
          tintColor={layoutConstants.colorPrimary}
        />
      ),
    });
    initTaskDetail();
  }, []);

  const saveAndBack = async () => {
    await handelSave();
    const category = stateRef.current.categoryUpdate;
    navigation.navigate("Actions", {
      action: "reload",
      category: category
        ? { id: category?.value, title: category?.label }
        : null,
    });
  };

  const initTaskDetail = async () => {
    const categories = await taskCategoryService.getCategoriesWithDefault();
    taskCategoryService.setCategories(categories);
  };

  const taskTimeDialogOnClose = () => {
    setTaskTimeDialogVisible(false);
  };

  const taskTimeDialogOnOpen = () => {
    setTaskTimeDialogVisible(true);
  };
  // On Done
  // dayRepeat DayRepeatModel, datePicked DateTimeModel, timePicked: DateTimeModel
  const taskTimeDialogOnDone = (dayRepeat, datePicked, timePicked) => {
    // put logic into mediator
    const mediator = new TaskRepeatMediator(dayRepeat, datePicked, timePicked);
    mediator.process();
    setDisplayDateTime(mediator.displayChip);
    setTaskTimeDialogRepeat(mediator.selectedDaysRepeat);
    setTaskTimeDialogDate(mediator.date);
    setTaskTimeDialogTime(mediator.time);
    taskTimeDialogOnClose();
  };

  React.useEffect(() => {
    if (task) {
      // on init existing task
      const mediator = new TaskRepeatRawDataMediator(
        taskTimeDialogRepeat,
        taskTimeDialogDate,
        taskTimeDialogTime
      );
      mediator.process();
      setDisplayDateTime(mediator.displayChip);
    } else {
      // on init new task
    }
  }, [task]);

  const handelSave = async () => {
    const id = task ? task.id : null;
    const {
      title,
      description,
      categoryUpdate,
      taskTimeDialogDate,
      taskTimeDialogTime,
      taskTimeDialogRepeat,
    } = stateRef?.current;
    let taskObj = new TaskModel({
      id,
      title: title,
      categoryId, // old category
      description: description,
      date: taskTimeDialogDate,
      time: taskTimeDialogTime,
      selectedDaysRepeat: taskTimeDialogRepeat,
    });
    TaskValidator.validate(taskObj);
    await taskService.saveTaskByCategory({
      categoryId: categoryUpdate?.value || categoryId,
      item: taskObj,
      isUpdate,
      dispatch,
    });
  };

  const onCloseChip = () => {
    log.debug("onCloseChip");
    // setDayRepeat(new DayRepeatModel());
    resetTime();
  };

  const resetTime = () => {
    setTaskTimeDialogDate(null);
    setTaskTimeDialogTime(null);
    setTaskTimeDialogRepeat(null);
    setDisplayDateTime(null);
  };

  const onDelete = async () => {
    navigation.navigate("Actions", { action: "delete", item: task });
  };

  return (
    <View style={styles.container} id="task-form">
      <HideKeyboard>
        <View style={{ flex: 1 }}>
          <TextInput
            variant="standard"
            value={title}
            onChangeText={setTitle}
            inputStyle = {{color: layoutConstants.textColor}}
            placeholder="Action Title"
          />
          <TextInput
            variant="standard"
            value={description}
            onChangeText={setDescription}
            inputStyle = {{color: layoutConstants.textColor}}
            placeholder="Action Description"
          />
          <View style={{ display: "flex", flexDirection: "row" }}>
            <Dropdown
              style={[styles.dropdown, isFocus && { borderColor: "blue" }]}
              placeholderStyle={styles.placeholderStyle}
              selectedTextStyle={styles.selectedTextStyle}
              inputSearchStyle={styles.inputSearchStyle}
              iconStyle={styles.iconStyle}
              data={displayCategories}
              search
              maxHeight={300}
              labelField="label"
              valueField="value"
              placeholder={!isFocus ? "Categories" : "..."}
              searchPlaceholder="Search..."
              value={categoryUpdate?.value || categoryId}
              onFocus={() => setIsFocus(true)}
              onBlur={() => setIsFocus(false)}
              onChange={(item) => {
                setCategoryUpdate(item);
                setIsFocus(false);
              }}
            />
            {/* <View
              style={{
                alignItems: "flex-end",
                justifyContent: "flex-end",
              }}
            >
              <Button
                variant="outlined"
                onPress={taskTimeDialogOnOpen}
                leading={(props) => <Icon name="plus" {...props} />}
                title="Time"
                style={{ width: 100 }}
              />
            </View> */}
          </View>
          {displayDateTime && (
            <Chip
              title={displayDateTime}
              onButtonPress={taskTimeDialogOnOpen}
              onIconPress={onCloseChip}
              titleStyle={{ flex: 1 }}
              style={{ marginTop: 10, borderRadius: 20 }}
            ></Chip>
          )}
          {taskTimeDialogVisible && (
            <TaskTimeDialog
              repeat={taskTimeDialogRepeat}
              visible={taskTimeDialogVisible}
              date={taskTimeDialogDate}
              time={taskTimeDialogTime}
              onClose={taskTimeDialogOnClose}
              onDone={taskTimeDialogOnDone}
            ></TaskTimeDialog>
          )}
        </View>
      </HideKeyboard>
      <View>
        {isUpdate && (
          <Button
            color="red"
            title={"Delete"}
            tintColor={"white"}
            onPress={onDelete}
            style={{ width: "100%", marginBottom: 20 }}
          />
        )}
        <Button
          disabled={!title}
          title={isUpdate ? "Update" : "Create"}
          onPress={saveAndBack}
          color={layoutConstants.colorPrimary}
          tintColor={"white"}
          style={{ width: "100%", marginBottom: 20 }}
        />
      </View>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    backgroundColor: "white",
    padding: 15,
    flex: 1,
  },
  dropdown: {
    height: 50,
    borderColor: "gray",
    borderBottomWidth: 1,
    paddingHorizontal: 8,
    marginRight: 20,
    flex: 1,
  },
  placeholderStyle: {
    fontSize: 16,
  },
  selectedTextStyle: {
    fontSize: 16,
  },
  iconStyle: {
    width: 20,
    height: 20,
  },
  inputSearchStyle: {
    height: 40,
    fontSize: 16,
  },
});
const mapState = (state) => {
  const categories = state.category.value;
  const displayCategories = categories?.map((c) => ({
    value: c.id,
    label: c.title,
  }));
  return {
    categories,
    displayCategories,
  };
};
export default connect(mapState)(TaskDetailScreen);
