// import { useIsFocused } from "@react-navigation/native";
import React, { useEffect } from "react";
import { View } from "react-native";
import { connect, useDispatch } from "react-redux";
import BottomBar from "utils/bottom-bar";
import EmptyPage from "utils/empty-page";
import { log } from "utils/log";
import RemovedSnackBar from "utils/removed-snack-bar";
import CategoryActionSheet from "../components/task/category-action-sheet";
import CategoryTab from "../components/task/category-tab";
import TaskCategoryForm from "../components/task/task-category-form";
import TaskList from "../components/task/task-list";
import {
  CategoryListValidator,
  CategoryValidator,
} from "../models/category/task-category-validator";
import { TaskListValidator } from "../models/task/task-validator";
import { createRemoveItemService } from "../services/remove-item-service";
import { createTaskCategoryService } from "../services/task-category-service";
import { createTaskService } from "../services/task-service";
import { HeaderBackButton } from "@react-navigation/elements";
import { constants, layoutConstants } from "../models/contants";

const TaskScreen = ({
  navigation,
  route,
  categories,
  tasks,
  activeCategory,
}) => {
  const [categoryUpdate, setCategoryUpdate] = React.useState(null);
  const [categoryVisible, setCategoryVisible] = React.useState(false);
  // CategoryActionSheet
  const [categoryActionSheetVisible, setCategoryActionSheetVisible] =
    React.useState(false);
  // RemovedSnackBar
  const [snackVisible, setSnackVisible] = React.useState(false);
  const [snackItem, setSnackItem] = React.useState(null);
  const { params } = route;
  // on start init
  useEffect(() => {
    navigation.setOptions({
      headerLeft: (props) => (
        <HeaderBackButton
          {...props}
          onPress={() => {
            navigation.navigate(constants.HOME_SCREEN);
          }}
          label={"Home"}
          labelVisible={true}
          tintColor={layoutConstants.colorPrimary}
        />
      ),
    });
    initTaskScreen();
    // Should validate task screen params
    validateScreen({ activeCategory, categories, tasks });
  }, []);
  useEffect(() => {
    if (params?.action === "delete") {
      onDeleteTask(params.item);
    }
    if (params?.action === "reload") {
      params?.category &&
        taskCategoryService.setActiveCategory(params.category);
    }
  }, [params]);
  // Services
  const taskService = createTaskService();
  const taskCategoryService = createTaskCategoryService();
  const removeItemService = createRemoveItemService();
  const dispatch = useDispatch();

  const onNavigateTaskScreen = (task) => {
    navigation.navigate("ActionDetail", {
      task: task ? JSON.stringify(task) : null,
      categoryId: task?.categoryId,
    });
  };

  const initTaskScreen = async () => {
    log.debug("initTaskScreen", activeCategory);
    const categories = await taskCategoryService.getCategoriesWithDefault();
    taskCategoryService.setCategories(categories);
    const category = activeCategory || categories[0];
    taskCategoryService.setActiveCategory(category);
    taskService.getTasksByCategoryStore(category.id);
  };

  const onDeleteTask = async (task) => {
    log.debug("onDeleteTask", task);
    await taskService.softRemoveTaskByCategory(task.categoryId, task);
    enableSnack(task);
  };

  // Category form
  const onCategoryOpen = () => {
    setCategoryVisible(true);
  };
  const onTaskTapAdd = async () => {
    setCategoryUpdate(null);
    onCategoryOpen();
  };

  const onCreateNewItem = () => {
    navigation.navigate(
      "ActionDetail",
      JSON.stringify({ categoryId: activeCategory.id })
    );
  };

  const onCategoryTapClick = async (tab) => {
    taskCategoryService.setActiveCategoriesAndLoadTasks(tab);
  };

  const onCloseCategoryForm = async () => {
    setCategoryVisible(false);
  };

  const onSaveCategoryForm = async (category, isUpdate) => {
    log.debug("onSaveCategoryForm", category, isUpdate);
    await taskCategoryService.saveCategories(category, isUpdate);
    onCloseCategoryForm();
    log.info("onSaveCategoryForm success");
  };

  const onDeleteCategoryForm = async (category) => {
    await onDeleteCategoryTab(category);
    onCloseCategoryForm();
  };

  const onDeleteCategoryTab = async (category) => {
    log.debug("onDeleteCategoryTab", category);
    await taskCategoryService.softRemoveCategories(category);
    enableSnack(category);
  };

  const onUpdateCategoryTab = async (category) => {
    log.debug("onUpdateCategoryTab", category);
    setCategoryUpdate(category);
    onCategoryOpen();
  };
  // End Category form

  const enableSnack = (removedItem) => {
    log.debug("enableSnack", removedItem);
    // validate remove item
    if (
      removedItem.constructor.name !== "TaskModel" &&
      removedItem.constructor.name !== "TaskCategoryModel"
    ) {
      return;
    }
    setSnackItem(removedItem);
    setSnackVisible(true);
    log.info("enableSnack success");
  };

  const onClickUndoRemovedSnackBar = async (item) => {
    log.debug("onClickUndoRemovedSnackBar", item);
    await removeItemService.undoRemoved({ item, dispatch });
  };

  const onDoneRemovedSnackBar = async (item, catergoryId) => {
    log.debug("onDoneRemovedSnackBar", item);
    await removeItemService.removePermanent(item);
  };

  // End RemovedSnackBar

  return (
    <View style={{ flex: 1, backgroundColor: "white" }} className="wrapper">
      {categories && (
        <CategoryTab
          tabs={categories}
          activeCategory={activeCategory}
          onAdd={onTaskTapAdd}
          onClick={onCategoryTapClick}
          onLongPress={() => {
            setCategoryActionSheetVisible(true);
          }}
        ></CategoryTab>
      )}
      {tasks.length > 0 ? (
        <TaskList
          tasks={tasks}
          onPress={(task) => onNavigateTaskScreen(task)}
        ></TaskList>
      ) : (
        <EmptyPage
          onPress={() => {
            onNavigateTaskScreen();
          }}
        ></EmptyPage>
      )}
      {categoryVisible && (
        <TaskCategoryForm
          category={categoryUpdate}
          onClose={onCloseCategoryForm}
          visible={categoryVisible}
          onSave={onSaveCategoryForm}
          onDelete={onDeleteCategoryForm}
        ></TaskCategoryForm>
      )}
      <CategoryActionSheet
        activeCategory={activeCategory}
        visible={categoryActionSheetVisible}
        setVisible={setCategoryActionSheetVisible}
        onDelete={onDeleteCategoryTab}
        onUpdate={onUpdateCategoryTab}
      ></CategoryActionSheet>
      <RemovedSnackBar
        visible={snackVisible}
        setVisible={setSnackVisible}
        item={snackItem}
        onUndo={onClickUndoRemovedSnackBar}
        onDone={onDoneRemovedSnackBar}
      ></RemovedSnackBar>
      <BottomBar
        onMenuClick={() => {
          setCategoryActionSheetVisible(true);
        }}
        onAddClick={onCreateNewItem}
        style={{ height: 600 }}
      ></BottomBar>
    </View>
  );
};

const validateScreen = (screenState) => {
  try {
    const { categories, tasks, activeCategory } = screenState;
    CategoryListValidator.validateSync(categories);
    TaskListValidator.validateSync(tasks);
    activeCategory && CategoryValidator.validateSync(activeCategory);
    // if(activeCategory && tasks[0] && activeCategory.id !== tasks[0].categoryId) {
    //   log.debug(activeCategory, tasks[0]);
    //   throw Error('task category is difference category');
    // }
    // tasks.map((t) => {
    //   if (activeCategory.id !== t.categoryId) {
    //     throw Error(
    //       "task category is difference category" +
    //         JSON.stringify({ activeCategory, tasks })
    //     );
    //   }
    // });
    log.info("validateScreen success");
  } catch (error) {
    log.error("validateScreen", error);
    throw error;
  }
};

const mapState = (state) => {
  const categories = state.category.value;
  const activeCategory = state.activeCategory.value;
  const tasks = state.task?.value?.filter((c) => !c.deleted);

  return {
    categories,
    activeCategory,
    tasks,
  };
};
export default connect(mapState)(TaskScreen);
