import { useIsFocused } from "@react-navigation/native";
import TitleBoxs from "components/home/title-boxs";
import { LinearGradient } from "expo-linear-gradient";
import React, { useEffect } from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import { useSelector } from "react-redux";
import BoxLabel from "../components/home/box-label";
import RecentTasks from "../components/home/recent-tasks";
import TrendingQuote from "../components/home/trending-quote";
import { log } from "../components/utils/log";
import { constants, layoutConstants } from "../models/contants";
import HomeService from "../services/home-service";
import TaskService from "../services/task-service";
import { lineStyle } from "../styles/component-style";
import TodayCalendar from "../components/home/today-calendar";
import { useOrientation } from "../components/utils/useOrientation";

const HomeScreen = ({ navigation }) => {
  const homeService = new HomeService();
  const taskService = new TaskService();
  const [categories, setCategories] = React.useState(null);
  const [dailyQuote, setdailyQuote] = React.useState(null);
  const tasks = useSelector((state) =>
    state.task.value.filter((c) => !c.deleted)
  );
  const focus = useIsFocused();
  const loading = new Array(8).fill("loading");
  const direction = useOrientation();
  const isLandscape = direction === constants.LANDSCAPE;

  useEffect(() => {
    const getCategoriesSync = async () => {
      const cate = await homeService.getCategories();
      setCategories(cate);
    };
    const getDailyQuoteSync = async () => {
      const dailyQuote = await homeService.getDailyQuote();
      setdailyQuote(dailyQuote);
    };

    getCategoriesSync();
    getDailyQuoteSync();
  }, []);

  useEffect(() => {
    focus && taskService.getTasksByCategoryStore(constants.RECENT_ACTIONS);
  }, [focus]);

  const onQuotePress = (tag) => {
    log.debug(tag);
    navigation.navigate("QuoteDetail", { url: tag.url });
  };

  const onDailyClick = () => {
    dailyQuote &&
      navigation.navigate("QuoteDetail", {
        quote: dailyQuote.content.item,
        owner: dailyQuote.content.owner,
      });
  };

  const containerStyle = [styles.container];
  isLandscape && containerStyle.push({ width: "70%", margin: "auto" });

  return (
    <View style={{ width: "100%", height: "100%" }}>
      <ScrollView>
        <View style={containerStyle}>
          <LinearGradient
            colors={[
              layoutConstants.colorPrimary,
              layoutConstants.colorSecondary,
              layoutConstants.bgColorPrimary,
            ]}
          >
            <View
              style={{
                alignItems: 'flex-end',
                justifyContent: 'flex-end',
                height: 100,
                width: 150
              }}
            >
              <TodayCalendar />
            </View>
            <BoxLabel
              style={styles.daily}
              label={dailyQuote && dailyQuote.content.item}
              onPress={onDailyClick}
              textStyle={styles.textDaily}
            ></BoxLabel>
            <RecentTasks navigation={navigation} tasks={tasks} />
          </LinearGradient>
          <TrendingQuote navigation={navigation}></TrendingQuote>

          {categories &&
            Object.keys(categories).map((k, index) => (
              <View key={index}>
                <View
                  style={{
                    ...lineStyle.line,
                    borderBottomColor: layoutConstants.bgColorPrimary,
                    marginHorizontal: 10,
                  }}
                />
                <TitleBoxs key={k} navigation={navigation} title={k}>
                  {categories[k] &&
                    categories[k].map((c, index) => {
                      c.tag = c.tag.charAt(0).toUpperCase() + c.tag.slice(1);
                      return (
                        <BoxLabel
                          key={index}
                          navigation={navigation}
                          label={c.tag}
                          onPress={() => onQuotePress(c)}
                          icon={c.icon}
                        ></BoxLabel>
                      );
                    })}
                </TitleBoxs>
              </View>
            ))}
          {!categories && loading && (
            <View>
              {[1, 2].map((key, i) => (
                <TitleBoxs key={i}>
                  {loading.map((item, index) => {
                    return <BoxLabel key={index}></BoxLabel>;
                  })}
                </TitleBoxs>
              ))}
            </View>
          )}
        </View>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: layoutConstants.bgColorPrimary,
    flex: 1,
    color: layoutConstants.colorPrimary,
  },
  daily: {
    height: 100,
    padding: 5,
    color: layoutConstants.textColorSecondary,
  },
  textDaily: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColor,
    fontSize: layoutConstants.quoteSize,
  },
});
export default HomeScreen;
