import {
  Button,
  DialogHeader,
  DialogActions,
  DialogContent,
  TextInput,
  IconButton,
} from "@react-native-material/core";
import * as React from "react";
import { View } from "react-native";
import TaskCategoryModel from "models/category/task-category-model";
import HideKeyboard from "../utils/hide-keyboard";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import Dialog from "../utils/dialog";

// data model: category || null
// view props: visible
// action: onClose, onSave, onDelete, onCancel
export default function TaskCategoryForm(props) {
  // on init create form, title are null
  // on init update form, title base on data model
  // on action create/update form, category is submited
  const { category, visible, onClose, onSave, onDelete } = props;
  const [title, setTitle] = React.useState(category?.title || "");
  const isUpdate = !!category;
  const [formTitle, setFormTitle] = React.useState("Create Category");

  React.useEffect(() => {
    if (category) {
      // on init existing category
      setFormTitle("Update Category");
    } else {
      // on init new category
      setFormTitle("Create Category");
    }
  }, [props.task]);

  const handelSave = () => {
    const id = category ? category.id : null;
    // todo validate category here
    let obj = new TaskCategoryModel({
      id,
      title,
    });
    onSave(obj, isUpdate);
  };

  const onDeleteHandler = () => {
    onDelete(category);
  };

  return (
    <Dialog visible={visible} onDismiss={onClose}>
      <HideKeyboard>
        <View>
          <View
            style={{ display: "flex", flexDirection: "row" }}
            id="task-form"
          >
            <View style={{ flex: 1 }}>
              <DialogHeader title={formTitle} />
            </View>
            {isUpdate && (
              <IconButton
                onPress={onDeleteHandler}
                icon={(props) => <Icon name="delete" {...props} />}
              />
            )}
            <IconButton
              onPress={onClose}
              icon={(props) => <Icon name="close" {...props} />}
            />
          </View>

          <DialogContent>
            <View>
              <TextInput
                value={title}
                onChangeText={setTitle}
                placeholder="Category Title"
              />
            </View>
          </DialogContent>

          <DialogActions>
            <Button variant="outlined" title="Cancel" onPress={onClose} />
            <Button
              disabled={!title}
              title={isUpdate ? "Update" : "Create"}
              onPress={handelSave}
            />
          </DialogActions>
        </View>
      </HideKeyboard>
    </Dialog>
  );
}
